<template lang="html">
  <content>
    <div class="container">

      <div class="box">
        <p>轉小數<br>Fraction to decimal</p>
        <input class='decimal-input' type="text" name="" v-model="numerator"><span>/</span>
        <input class='decimal-input' type="text" name="" v-model="denominator" >
        <p>{{fraction}}</p>
      </div>
      <div class="box">
        <p>算面積<br>Calculate area</p>
        <div v-for='input, index in inputs'>
          <span>{{index + 1}}. </span>
          <input type="text" placeholder="" v-model="input.parm1" v-on:click="calThis(index)" >
          <input type="text" placeholder="" v-model="input.parm2" v-if="!inputs[index+1]" v-on:click.once="addInput(index)">
          <input type="text" placeholder="" v-model="input.parm2" v-else>
          <p >{{input.res}} ft<span class='sqf'>2</span></p>
          <!-- <button type="button" name="button" v-if="!inputs[index+1]" v-on:click.once="addInput(index)">Add</button> -->
        </div>


      </div>

      <div class="">
        <input class="cost" type="text" placeholder="Cost per ft2" v-model='cost'>
        <button type="button" name="button" v-on:click="getResult()">總數/total</button>
        <button type="button" name="button" v-on:click="reset()">重置/reset</button>
      </div>
      <div class="results">
        <p>總面積/total area： {{result}} ft<span class='sqf'>2</span></p>
        <p>總價/total price： ${{totalCost}}</p>
      </div>
      </div>


    </content>
</template>

<script>
export default {
  name: 'calculator',
  data() {
    return {
      inputs: [{parm1: '', parm2: '', res: 0}],
      result: 0,
      numerator: 1,
      denominator: 16,
      cost: 0,
      totalCost: 0,
    }
  },
  computed: {
    fraction: function(){
      return this.numerator / this.denominator
    },
  },
  methods: {
    addInput: function (index) {
      this.inputs[index].res = Number((this.inputs[index].parm1 * this.inputs[index].parm2 / 144)).toFixed(5)/1
      this.inputs.push({parm1:'', parm2:'',res: 0})
    },
    calThis: function(index) {
      this.inputs[index-1].res = Number(this.inputs[index-1].parm1 * this.inputs[index-1].parm2 / 144).toFixed(5)/1
    },
    reset: function() {
      this.inputs= [{parm1: '', parm2: '', res: 0}]
      this.cost= 0
      this.totalCost= 0
      this.result = 0
    },
    getResult: function() {
      this.result = 0

      for (var i=0; i<this.inputs.length; i++) {
        if (this.inputs[i].parm1 === '' || this.inputs[i].parm2 ==='') {
          this.inputs.splice(i, 1)
        }
          this.inputs[i].res =  Number(this.inputs[i].parm1 * this.inputs[i].parm2 / 144).toFixed(5)/1
          this.result += (this.inputs[i].res).toFixed(5)/1;
          if(this.cost !== 0 && this.cost > 5) {
            this.totalCost = (this.result * this.cost).toFixed(2)/1
          }
        }
    },
  }
}
</script>

<style lang="css">
  input {
    width: 20%;
    margin: 6px 5px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid black;
    text-align: center;
  }
  p {
    margin: 4px 0
  }
  input:focus {
    border: none;
    border-radius: 5px;
  }

  button {
    padding: 10px 14px;
    margin: 10px 0;
  }

  .container {
    width: 320px;
    margin: 0 auto;
  }
  .box {
    border: 1px solid black;
    border-radius: 4px;
    padding: 5px 10px;
    margin: 5px 0
  }
  .sqf {
    font-size: 70%;
    vertical-align: text-top;
  }

  .decimal-input {
    width: 10%;
  }

  .results {
    text-align: left;
    font-size: 120%;

  }

</style>



